<template>
  <div>
    <div
      v-if="isHome"
      style="
        display: flex;
        flex-direction: column;
        height: 80vh;
        justify-content: space-between;
      "
    >
      <p>选择难度并开始：</p>
      <button @click="handleClick(0)">简单</button>
      <button @click="handleClick(1)">中等</button>
      <button @click="handleClick(2)">困难</button>
    </div>
    <div v-else><sheep :level="level" /></div>
  </div>
</template>

<script>
import Sheep from "./Sheep.vue";
export default {
  data() {
    return {
      level: 0,
      isHome: true,
    };
  },
  components: { Sheep },
  methods: {
    handleClick(level) {
      this.level = level;
      this.isHome = false;
    },
  },
};
</script>

<style>
p {
  font-size: 10vw;
  text-align: center;
  margin: 0;
}

button {
  padding-bottom: 10vw;
  margin: auto;
  font-size: 10vw;
  width: 70vw;
  height: 20vw;
  text-align: center;
  line-height: 20vw;
  border-radius: 5vw;
  border: solid transparent;
}
</style>


